'use strict';

import React from 'react';

class Audience extends React.Component {
    constructor(props, state) {
        super(props);
        super(state);
        this.displayName = 'Audience';
    }
    render() {
        return <div>Audience : {this.state.title}</div>;
    }
}

export default Audience;



